html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
footer {
  margin: 0.5em 0;
  text-align: center;
}
#map {
  height: 100vh;
}
#sidepanel {
  position: absolute;
  top: 50px;
  left: -220px;
  bottom: 0;
  width: 200px;
  background-color: #FFF;
  box-shadow: 10px 0px 20px 0px rgba(0, 0, 0, 0.2);
  transition: left ease-in-out 0.3s;
}
#sidepanel.onscreen {
  left: 0;
}
#sidepanel #filter img {
  width: 24px;
  margin-left: 5px;
}
#sidepanel #hide-filter {
  margin-top: 15px;
  font-size: 2em;
}
header {
  width: 100%;
  background-color: #FFF;
  position: absolute;
  top: 0;
  z-index: 100;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.4);
  border-bottom: 1px solid #DDD;
}
h1 {
  width: 100%;
  text-align: center;
  margin: 0.5em 0;
}
h2 {
  text-align: center;
}
.bubble {
  text-align: center;
  color: #333;
  padding: 10px 0 10px 20px;
}
.bubble strong {
  font-size: 1.6rem;
}
.bubble .info {
  margin-top: 0.5em;
}
nav.navbar {
  margin-bottom: 0;
  border-width: 0;
}
.navbar-header {
  font-weight: bold;
}
.navbar-default .navbar-brand {
  color: #111;
}
.navbar-default .navbar-brand:focus,
.navbar-default .navbar-brand:hover {
  color: #333;
}
.navbar-default .navbar-nav > li > a {
  color: #111;
}
.navbar-default .navbar-nav > li > a:focus,
.navbar-default .navbar-nav > li > a:hover {
  color: #333;
}
